import Carousel from 'react-bootstrap/Carousel';
import logo from './assignimg/main-banner-1.JPG'
import logoo from './assignimg/main-banner.jpg'
import log from './assignimg/blog-1.jpg'
import logg from './assignimg/asasass.JPG'
import { Link } from 'react-router-dom';
import Button from 'react-bootstrap/Button';


function CarouselFadeExample() {
  return (
  <div>
    <Carousel fade>
      <Carousel.Item>
        <img
          className="d-block w-100"
          src={logo}
          alt="First slide"
        />
        <Carousel.Caption>
          <h3>     </h3>
          <p>         </p>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="d-block w-100"
          src={log}
          alt="Second slide"
        />

        <Carousel.Caption>
          <h3> </h3>
          <p>            </p>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="d-block w-100 h"
          src={logoo}
          alt="Third slide"
        />

        <Carousel.Caption>
          <h3> </h3>
          <p>      </p>
          <Button variant="primary" ><Link to="/" style={{color: '#ffffff',textDecoration:'none'}}> </Link></Button>

        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="d-block w-100"
          src={logg}
          alt="Third slide"
        />

        <Carousel.Caption>
          <h3>  </h3>
          <p>              </p>
          <Button variant="primary" ><Link to="/" style={{color: '#ffffff',textDecoration:'none'}}> </Link> </Button>

        </Carousel.Caption>

      </Carousel.Item>
    </Carousel>
    <h3 className='bg-primary' style={{
      textAlign:'center',
      color:'#fff',
      padding:'5px 0'
    }}> </h3>


    </div>
  );
}

export default CarouselFadeExample;